.navbar_container {
  --color_nav_bg: #381030;
  --color_nav_txt: rgb(100, 116, 139);
  --color_nav_ctg: rgb(226, 232, 240);
  --color_nav_drop: #5d2f45;
  //   --color_nav_active: rgb(85, 79, 232);
  //   --color_nav_not_active: rgb(61, 74, 94);
  --color_nav_active_drop_bg: #ea0e76;
  --color_nav_active_bg: #ea0e76;

  --nav_width: 17rem;

  background: var(--color_nav_bg);
  height: 100vh;
  box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: var(--padding-md) 0;
  transition: transform 300ms ease-in-out;

  @media screen and (max-width: 1024px) {
    transform: translateX(-100%);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 12;
  }

  @media screen and (max-width: 18rem) {
    width: 100vw;
  }
}

.navbar_mobile_active {
  @media screen and (max-width: 1024px) {
    transform: translateX(0);
    color: white;
  }
}

nav {
  position: relative;
  width: var(--nav_width);
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  transition: width linear 0.3s;

  &::-webkit-scrollbar {
    display: none;
  }
}

.logo {
  padding: 0 0.35rem;
  font-size: 2.2rem;
  display: flex;
  .logo_icon {
    color: var(--color_nav_active);
  }

  .mobile_cancel_icon {
    display: none;
  }

  @media screen and (max-width: 1024px) {
    .logo_icon {
      display: none;
    }

    .mobile_cancel_icon {
      display: block;
      cursor: pointer;
      font-size: 2.2rem;
      padding: 0.2rem;
      color: var(--color_nav_active);
    }
  }
}

.menu_container {
  margin-top: var(--margin-lg);
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.categories {
  color: var(--color_nav_txt);
  text-transform: uppercase;
  font-size: 0.8rem;
  margin-bottom: var(--margin-xxs);

  svg {
    font-size: 1rem;
  }
}

.second_category {
  margin-top: 3rem;
}

// DROPDOWN CSS

.dropdown {
  background: var(--color_nav_drop);

  svg {
    color: var(--color_nav_active);
  }
}

.dropdown a {
  display: flex;
  align-items: center;
  flex: 1;
  padding: 0.5rem 0.75rem;
  text-decoration: none;
  color: var(--color_nav_ctg);
  transition: all ease-in-out 0.2s;

  .description {
    margin-left: 0.5rem;
    font-weight: 400;
    transition: opacity 200ms ease;
  }

  svg {
    font-size: 1.5rem;
    color: var(--color_nav_not_active);
    flex-shrink: 0;
  }

  &:hover {
    background: var(--color_nav_active_drop_bg) !important;
  }

  &:hover svg {
    color: var(--color_nav_active);
  }
}

.dropdown .active {
  background: var(--color_nav_active_drop_bg) !important;

  svg {
    color: var(--color_nav_active);
  }
}

.li_navlink a {
  display: flex;
  align-items: center;
  flex: 1;
  padding: 0.5rem 1.3rem;
  text-decoration: none;
  color: var(--color_nav_ctg);
  transition: all ease-in-out 0.2s;

  .description {
    margin-left: 0.5rem;
    font-weight: 400;
    transition: opacity 200ms ease;
  }

  svg {
    font-size: 1.5rem;
    color: var(--color_nav_not_active);
    flex-shrink: 0;
  }

  &:hover {
    background: var(--color_nav_active_bg);
  }

  &:hover svg {
    color: var(--color_nav_active);
  }
}

.li_navlink .active {
  background: var(--color_nav_active_bg);

  svg {
    color: var(--color_nav_active);
  }
}

.made_love {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  height: 80vh;

  img {
    height: 55px;
  }
}

.btn_logout {
  margin-top: auto;
  display: flex;
  justify-content: flex-end;
  transition: all ease-in-out 200ms;
  padding: 0.5rem 0.75rem 0 0.75rem;

  svg {
    font-size: 1.5rem;
    color: var(--color_nav_active);
    cursor: pointer;
    transform: scaleX(-1);
  }
}

.mobile_nav_background_active {
  width: 0;
  height: 100vh;
  transition: all ease-out 500ms;
  transition-delay: 300ms;
  cursor: pointer;
  @media screen and (max-width: 1024px) {
    display: block;
    position: absolute;
    top: 0;
    left: calc(var(--nav_width));
    width: calc(100vw - var(--nav_width));
    background: rgba(0, 0, 0, 0.185);
    z-index: -1;
  }
}

