@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");

body {
  --margin-xl: 4rem;
  --margin-lg: 3rem;
  --margin-md: 2rem;
  --margin-sm: 1rem;
  --margin-xs: 0.8rem;
  --margin-xxs: 0.5rem;

  --padding-xl: 4rem;
  --padding-lg: 3rem;
  --padding-md: 2rem;
  --padding-sm: 1rem;
  --padding-xs: 0.8rem;
  --padding-xxs: 0.5rem;

  font-family: "Poppins", sans-serif;
  overflow: hidden;
  background: rgb(223, 223, 223);
}

.App {
  display: flex;
}

/* Disabling the annoying scroll feature on input number */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* REMOVE AUTOFILL STYLES */
input:-webkit-autofill,
input:-webkit-autofill:focus {
  transition: background-color 600000s 0s, color 600000s 0s;
  font-weight: 100;
}

/* MODAL */
.custom-modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 80;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background-color: white;
  border-radius: 15px;
  border: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
  animation: appear 350ms ease-in 1;
}

@keyframes appear {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
}

/* FOR OVERIDING DATA TABLE STYLES */

.MuiInput-underline:before {
  left: 0;
  right: 0;
  bottom: 0;
  content: "\00a0";
  position: absolute;
  transition: none !important;
  border-bottom: none !important;
  pointer-events: none;
}

.MuiIconButton-root:hover {
  background: none !important;
}

.MTableHeader-header-13 {
  z-index: 2 !important;
}

.MuiSelect-selectMenu {
  border: 1px solid #cccccc !important;
  padding: 10px !important;
  border-radius: 3px !important;
}

.MuiTablePagination-select {
  text-align: right !important;
  border: none !important;
  padding-left: 8px !important;
  padding-right: 24px !important;
  text-align-last: right;
}

.MuiInput-underline:after {
  left: 0;
  right: 0;
  bottom: 0;
  content: "\00a0";
  position: absolute;
  transition: none !important;
  border-bottom: none !important;
  pointer-events: none;
}

.MuiTableCell-root {
  border-bottom: none !important;
}

.MuiTableCell-head {
  border-bottom: 1px solid #cccccc !important;
}

/* MATERIAL TABLE ICON STYLES */
.delete-icon {
  background: #ff959538;
  color: #ff9595;
  transition: 0.3s;
}

.delete-icon:hover {
  box-shadow: 0px 0px 6px 0.1px #ff9595;
}

.edit-icon {
  background: #32a87538;
  color: #32a875;
  transition: 0.3s;
}

.edit-icon:hover {
  box-shadow: 0px 0px 6px 0.1px #32a875;
}

.arrow-icon {
  background: #65866438;
  color: #658664;
  transition: 0.3s;
}

.arrow-icon:hover {
  box-shadow: 0px 0px 6px 0.1px #658664;
}

.re-enable-icon {
  background: #00799e38;
  color: #00799e;
  transition: 0.3s;
}

.re-enable-icon:hover {
  box-shadow: 0px 0px 6px 0.1px #00799e;
}

/* HOVER BUTTONS */

.red {
  background: #de5151;
  transition: 0.3s;
}

.red:hover {
  box-shadow: 0px 0px 5px 0.3px #de5151;
}

.red-dark {
  background: #8b4343;
  transition: 0.3s;
}

.red-dark:hover {
  box-shadow: 0px 0px 5px 0.3px #8b4343;
}

.green {
  background: #91f086;
  transition: 0.3s;
}

.green:hover {
  box-shadow: 0px 0px 5px 0.3px #91f086;
}

.green-dark {
  background: #72af3a;
  transition: 0.3s;
}

.green-dark:hover {
  box-shadow: 0px 0px 5px 0.3px #72af3a;
}

.grey {
  background: #9f9f9f;
  transition: 0.3s;
}

.grey:hover {
  box-shadow: 0px 0px 5px 0.3px #9f9f9f;
}

.black {
  background: #282f23;
  transition: 0.3s;
}

.black:hover {
  box-shadow: 0px 0px 5px 0.3px #282f23;
}

.pink {
  background: #ea0e76;
  transition: 0.3s;
}

.pink:hover {
  box-shadow: 0px 0px 5px 0.3px #ea0e76;
}

.primary {
  color: #381030;
}

.secondary {
  color: #ea0e76;
}

/* FOR INPUT FIELDS */
.input-form {
  border: 1px solid #00000054;
  padding: 0.5rem 1rem;
  border-radius: 0.3rem;
  width: 100%;
}

.input-calendar {
  border: 1px solid #cccccc;
  padding: 10px;
  font-size: 18px;
  width: 100%;
  border-radius: 10px;
}

/* PAGINATION */
.pagination {
  justify-content: center;
  display: flex;
  padding-left: 0;
  list-style: none;
}

.page-item .page-link {
  position: relative;
  display: block;
  margin: 0 5px;
  min-height: 40px;
  min-width: 40px;
  border-radius: 20px;
  text-align: center;
  line-height: 40px;
  color: #381030;

  text-decoration: none;
}

.page-item a.page-link:hover {
  background-color: #cccccc;
}

.page-item.active .page-link {
  font-weight: 700;
  color: #ffffff;
  background-color: #ea0e76;
}

.page-item.disabled .page-link {
  color: #6c757d;
  pointer-events: none;
  cursor: auto;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
